"use server"
import Razorpay from "razorpay"
import Payment from "@/models/Payment"
import connectDB from "@/db/connectDb"
import User from "@/models/User"

export const initiatePayment = async (amount, to_username, paymentform) => {
    await connectDB()
    const instance = new Razorpay({
        key_id: process.env.NEXT_PUBLIC_RAZORPAY_KEY_ID,
        key_secret: process.env.RAZORPAY_SERET_KEY,
    })

    let options = {
        amount: Number.parseInt(amount),
        currency: "INR",
    }

    let order = await instance.orders.create(options)
    await Payment.create({
        amount: amount / 100,
        order_id: order.id,
        to_user: to_username,
        name: paymentform.name,
        message: paymentform.message,
    })

    return order
}

export const fetchuser = async (username) => {
    await connectDB()
    const user = await User.findOne({ username:username }).lean() 
    if (!user) return null
    return {
        ...user,
        _id: user._id.toString(),
        createdAt: user.createdAt?.toISOString(),
        updatedAt: user.updatedAt?.toISOString()
    }
}

export const fetchPayments = async (username) => {
    await connectDB()
    let payments = await Payment.find({ to_user: username }).sort({ amount: -1 }).lean()
    return payments.map(p => ({
        ...p,
        _id: p._id.toString(),
        createdAt: p.createdAt?.toISOString(),
        updatedAt: p.updatedAt?.toISOString()
    }))
}

export const fetchdone = async (username) => {
    await connectDB()
    let done = await Payment.find({ to_user: username, done: true }).lean()
    return done.map(d => ({
        ...d,
        _id: d._id.toString(),
        createdAt: d.createdAt?.toISOString(),
        updatedAt: d.updatedAt?.toISOString()
    }))
}